'use strict';

/**
* Test if a value is a symbol.
*
* @module @stdlib/assert-is-symbol
*
* @example
* import Symbol from '@stdlib/symbol-ctor';
* import isSymbol from '@stdlib/assert-is-symbol';
*
* var bool = isSymbol( Symbol( 'beep' ) );
* // returns true
*
* bool = isSymbol( Object( Symbol( 'beep' ) ) );
* // returns true
*
* bool = isSymbol( {} );
* // returns false
*
* @example
* import Symbol from '@stdlib/symbol-ctor';
* import { isPrimitive as isSymbol } from '@stdlib/assert-is-symbol';
*
* var bool = isSymbol( Symbol( 'beep' ) );
* // returns true
*
* bool = isSymbol( Object( Symbol( 'beep' ) ) );
* // returns false
*
* bool = isSymbol( {} );
* // returns false
*
* @example
* import Symbol from '@stdlib/symbol-ctor';
* import { isObject as isSymbolObject } from '@stdlib/assert-is-symbol';
*
* var bool = isSymbolObject( Symbol( 'beep' ) );
* // returns false
*
* bool = isSymbolObject( Object( Symbol( 'beep' ) ) );
* // returns true
*
* bool = isSymbolObject( {} );
* // returns false
*/

// MODULES //

import hasSymbols from '@stdlib/assert-has-symbol-support';
import main from './main.js';
import polyfill from './polyfill.js';


// MAIN //

var isSymbol;
if ( hasSymbols() ) {
	isSymbol = main;
} else {
	isSymbol = polyfill;
}


// EXPORTS //

export default isSymbol;

// exports: { "isPrimitive": "isSymbol.isPrimitive", "isObject": "isSymbol.isObject" }
